import PropTypes from 'prop-types'
import styled from 'styled-components'
import theme from '../themes/portfolio'

const StyledChip = styled.div``

const StyledChipImg = styled.img`
  height: 20rem;
  width: 20rem;
`

const StyledChipTitle = styled.div``

const StyledChipDetails = styled.div``

const Chip = ({ details, src, title }) => (
  <StyledChip>
    <StyledChipImg src={src} alt="icon" />
    <StyledChipTitle>{title}</StyledChipTitle>
    {details.map(deet => (
      <StyledChipDetails>{deet}</StyledChipDetails>
    ))}
  </StyledChip>
)

Chip.propTypes = {
  details: PropTypes.arrayOf(PropTypes.object).isRequired,
  src: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired
}

export default Chip
